<template>
  <div class="tabbar-class">
    <van-tabbar
      fixed
      placeholder
      v-model="active" 
      active-color="#41ae3c" 
      inactive-color="#515151"
      safe-area-inset-bottom
      >
        <van-tabbar-item 
          v-for="(item,index) of tabs"
          :key="item.id"
          @click.native="onClickTab(index)"
          replace
          :to="(item.route)"
          :icon="item.icon" 
          >
            <span>{{item.title}}</span>
                <!-- <template #icon="props">
                <img :src="props.active ? item.active : item.normal" />
                </template> -->
        </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant'
import 'vant/lib/tabbar/style'
import 'vant/lib/tabbar-item/style'

export default {
  name: 'Tabbar',
  components: {
    'van-tabbar': Tabbar,
    'van-tabbar-item': TabbarItem
  },
  data() {
    return {
      active: 0,
      tabs:[
        {
            id: 100,
            route:'/home',
            title: '首页',
            icon: 'home-o',
            // normal: require("imgs/tab_home.png"),
            // active: require("imgs/tab_home_s.png")
        },
        {
            id: 101,
            route:'/mine',
            title: '我的',
            icon: 'user-o',
            // normal: require("imgs/tab_mine.png"),
            // active: require("imgs/tab_mine_s.png")
        }
      ]
    };
  },
  mounted() {
    // 如有缓存，刷新页面时还进入该页面
    let index = this.$store.state.tabIndex
    if(index){
        this.onClickTab(index)
    }
  },
  methods: {
    onClickTab (index) {
      index = Number(index)
      this.active = index
      // 改变共用数据 记录当前tab页
      // this.$store.dispatch('changeCity', city)
      // 也可以直接commit 不用去store实现action
      this.$store.commit('changeTabIndex', index)

      // 加载相应页面 不用自己跳转，自己会跳转
      // let val = this.tabs[index].route
      // this.$router.push(val)
    }
  },
  watch: {
    '$route'(to) {
      const tabIndex = this.tabs.findIndex(tab => tab.route === to.path)
      if (tabIndex !== -1) {
        this.active = tabIndex
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
// /deep/ .van-hairline--top-bottom .van-tabbar .van-tabbar--fixed
//   z-index 1000
.van-tabbar
  left 0
  right 0
  bottom 0
  z-index 1000
  position: fixed
</style>